import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

function Home({ stages }) {
    function filter_array(test_array) {
        var index = -1,
            arr_length = test_array ? test_array.length : 0,
            resIndex = -1,
            result = [];
        while (++index < arr_length) {
            var value = test_array[index];

            if (value) {
                result[++resIndex] = value;
            }
        }
        return result;
    }

    let currentWeek = filter_array(stages.map((stage) => {
        return stage.weeks.find((week) => {
            return week.startDate < Date.now() && week.endDate > Date.now()
        })
    }))[0]
    let nextMatch = currentWeek.matches.find((match) => {
        return match.startDateTS > Date.now();
    })

    console.log(currentWeek);
    console.log(nextMatch);

    return (
        // <div className="Home" dangerouslySetInnerHTML={{__html: home[0]}}>
        // </div>
        <div className="Home">
            <Timer nextMatch={nextMatch} />
            {/* <div className="alert">
                <p>Bienvenue sur OW-BET,</p>
                <p>Ce site est actuellement en BETA fermé (d'où l'adresse un peu bizarre).</p>
                <p>Si vous trouvez un bug ou tout autre comportement inhabituel, faite le remonter a Hystérias</p>
            </div> */}
            <div className="normal">
                Le site est actuellement en BETA inscrit toi pour en savoir plus et participer a ce projet.
            </div>

            {(cookie.load('login') ? <div className="alert">
                <ul>
                    <li><s>Ajout d'un Chat</s> (Problème avec le chat qui fait crash le serv donc je l'ai viré)</li>
                    <li>Ajout des bets passé dans le profil public</li>
                    <li>Acces au profil public des joueurs</li>
                    <li>Ajout de plusieur HF</li>
                    <li>Correction de bug si et la</li>
                </ul>

            </div> : '')}

        </div>
    )
}
function Timer({ nextMatch }) {
    const [timer, setTimer] = useState(nextMatch.startDateTS);
    let total_secondes = (timer - Date.now()) / 1000;
    let jours = Math.floor(total_secondes / (60 * 60 * 24));
    let heures = Math.floor((total_secondes - (jours * 60 * 60 * 24)) / (60 * 60));
    let minutes = Math.floor((total_secondes - ((jours * 60 * 60 * 24 + heures * 60 * 60))) / 60);
    let secondes = Math.floor(total_secondes - ((jours * 60 * 60 * 24 + heures * 60 * 60 + minutes * 60)));
    setTimeout(() => {
        setTimer(timer);
    }, 1000);
    console.log(nextMatch);
    return (
        <div className="Timer">
            <div className="body">
                <div className="text">Prochain Match</div>
                <div className="team"><img src={`./imgs/teams/${nextMatch.competitors[0].abbreviatedName}.png`} alt="" /></div>
                <div className="timer">
                    {(jours > 0) ? ` ${jours} jours` : ''}{(heures > 0) ? ` ${heures}:` : ''}{(minutes > 0) ? `${minutes}:` : ''}{(secondes > 0) ? `${secondes}` : ''}
                </div>
                <div className="team"><img src={`./imgs/teams/${nextMatch.competitors[1].abbreviatedName}.png`} alt="" /></div>
            </div>

        </div>
    )
}

export default withRouter(Home);